"use strict";

window.addEventListener("load", function () {

	// store tabs variables
	var tabs = document.querySelectorAll("ul.nav-tabs > li");

	for (var i = 0; i < tabs.length; i++) {
		tabs[i].addEventListener("click", switchTab);
	}

	function switchTab(event) {
		event.preventDefault();

		document.querySelector("ul.nav-tabs li.active").classList.remove("active");
		document.querySelector(".tab-pane.active").classList.remove("active");

		var clickedTab = event.currentTarget;
		var anchor = event.target;
		var activePaneID = anchor.getAttribute("href");

		clickedTab.classList.add("active");
		document.querySelector(activePaneID).classList.add("active");
	}

	var spans = document.querySelectorAll('.word span');

	console.log(spans);
	spans.forEach(function (span, idx) {
		span.addEventListener('click', function (e) {
			e.target.classList.add('active');
		});
		span.addEventListener('animationend', function (e) {
			e.target.classList.remove('active');
		});

		// Initial animation
		setTimeout(function () {
			span.classList.add('active');
		}, 750 * (idx + 1));
	});
});

/* Sync btn */
document.addEventListener('DOMContentLoaded', function (e) {
	var testimonialForm = document.getElementById('careerist-sync-trigger-form');

	testimonialForm.addEventListener('submit', function (e) {
		e.preventDefault();

		// reset the form messages
		resetMessages();

		// collect all the data
		var data = {
			nonce: testimonialForm.querySelector('[name="nonce"]').value

			// validate everything
			// ajax http post request
		};var url = testimonialForm.dataset.url;
		var params = new URLSearchParams(new FormData(testimonialForm));

		testimonialForm.querySelector('.js-form-submission').classList.add('show');

		fetch(url, {
			method: "POST",
			body: params
		}).then(function (res) {
			return res.json();
		}).catch(function (error) {
			resetMessages();
			testimonialForm.querySelector('.js-form-error').classList.add('show');
		}).then(function (response) {
			resetMessages();

			if (response === 0 || response.status === 'error') {
				testimonialForm.querySelector('.js-form-error').classList.add('show');
				return;
			}

			testimonialForm.querySelector('.js-form-success').classList.add('show');
			testimonialForm.reset();
		});
	});
});

function resetMessages() {
	document.querySelectorAll('.field-msg').forEach(function (f) {
		return f.classList.remove('show');
	});
}

function validateEmail(email) {
	var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
	return re.test(String(email).toLowerCase());
}

